<template>
  <div class="container">
    <h2 class="text-center">註冊頁面</h2>
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <form class="row">
      <div class="col-12">
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="studentIDInput" v-bind:class="{ 'is-invalid': studentIDErr }" v-model="studentID" placeholder="學號" />
          <label for="studentIDInput">學號(依實際填寫)</label>
          <div class="invalid-feedback text-end">{{ studentIDErrMsg }}</div>
        </div>
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="userNameInput" v-bind:class="{ 'is-invalid': userNameErr }" v-model="userName" placeholder="名稱" />
          <label for="userNameInput">名稱(中文2~5字)</label>
          <div class="invalid-feedback text-end">{{ userNameErrMsg }}</div>
        </div>
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="accountInput" v-bind:class="{ 'is-invalid': accountErr }" v-model="account" placeholder="帳號" />
          <label for="accountInput">帳號(英數5碼)</label>
          <div class="invalid-feedback text-end">{{ accountErrMsg }}</div>
        </div>
        <div class="form-floating mb-3">
          <input
            type="new-password"
            class="form-control"
            name="passwordInput"
            v-bind:class="{ 'is-invalid': passwordErr }"
            v-model="password"
            placeholder="密碼"
          />
          <label for="passwordInput">密碼(大小英數8碼)</label>
          <div class="invalid-feedback text-end">{{ passwordErrMsg }}</div>
        </div>
        <div class="form-floating mb-3">
          <input
            type="current-password"
            class="form-control"
            name="passwordChkInput"
            v-bind:class="{ 'is-invalid': passwordChkErr }"
            v-model="passwordChk"
            placeholder="密碼確認"
          />
          <label for="passwordChk">密碼確認</label>
          <div class="invalid-feedback text-end">{{ passwordChkErrMsg }}</div>
        </div>
      </div>
      <div class="col-6 d-grid gap-2">
        <button class="btn btn-primary btn-lg" type="button" @click="register">註冊</button>
      </div>
      <div class="col-6 d-grid gap-2">
        <button class="btn btn-secondary btn-lg" type="button" @click="$router.push('/')">取消</button>
      </div>
    </form>
  </div>
</template>

<script>
import Alert from "@/components/Alert.vue";

export default {
  name: "Register",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: -1,
      // 註冊資料與驗證
      studentID: "",
      studentIDErr: false,
      studentIDErrMsg: "",
      userName: "",
      userNameErr: false,
      userNameErrMsg: "",
      account: "",
      accountErr: false,
      accountErrMsg: "",
      password: "",
      passwordErr: false,
      passwordErrMsg: "",
      passwordChk: "",
      passwordChkErr: false,
      passwordChkErrMsg: "",
    };
  },
  methods: {
    register() {
      if (this.studentID.trim() === "") {
        this.studentIDErr = true;
        this.studentIDErrMsg = "請輸入學號";
      }
      if (this.userName.trim() === "") {
        this.userNameErr = true;
        this.userNameErrMsg = "請輸入名稱";
      }
      if (this.account.trim() === "") {
        this.accountErr = true;
        this.accountErrMsg = "請輸入帳號";
      }
      if (this.password.trim() === "") {
        this.passwordErr = true;
        this.passwordErrMsg = "請輸入密碼";
      }
      if (this.passwordChk.trim() === "") {
        this.passwordChkErr = true;
        this.passwordChkErrMsg = "請輸入密碼確認";
      }
      if (!this.studentIDErr && !this.userNameErr && !this.accountErr && !this.passwordErr && !this.passwordChkErr) {
        this.$http
          .post("api/users/register", {
            studentID: this.studentID,
            name: this.userName,
            account: this.account,
            password: this.password,
          })
          .then((res) => {
            if (res.data.code === 200) {
              // 訊息提示框
              this.alertVisible = true;
              this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
              this.funcNum = 2;
              this.MSG.TYPE = "primary"; // primary成功, warning警告, danger錯誤
              this.MSG.TITLE = "系統提示"; // 系統提示，系統警告，系統錯誤
              this.MSG.TEXT = res.data.msg; // 內容
            } else if (res.data.code === 400) {
              // 訊息提示框
              this.alertVisible = true;
              this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
              this.funcNum = 1;
              this.MSG.TYPE = "danger"; // primary成功, warning警告, danger錯誤
              this.MSG.TITLE = "系統錯誤"; // 系統提示，系統警告，系統錯誤
              this.MSG.TEXT = res.data.msg; // 內容
            }
          })
          .catch((err) => {
            this.alertVisible = true;
            this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
            this.funcNum = 1;
            this.MSG.TYPE = "danger"; // primary成功, warning警告, danger錯誤
            this.MSG.TITLE = "系統錯誤"; // 系統提示，系統警告，系統錯誤
            this.MSG.TEXT = [err]; // 內容
          });
      }
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  mounted() {},
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === 1) {
            // 功能1-[提示視窗關閉]
            // 頁面處理
            this.hideAlert();
          } else if (this.funcNum === 2) {
            // 功能2-[修改用戶參數-後續處理]
            this.hideAlert();
            this.$router.push("/");
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },
    studentID: function () {
      const isText = /^\d{1,3}$/;
      if (!isText.test(this.studentID)) {
        this.studentIDErr = true;
        this.studentIDErrMsg = "僅接受三位數「數字」";
      } else {
        this.studentIDErr = false;
      }
    },
    userName: function () {
      if (this.userName.length < 2 || this.userName.length > 5) {
        this.userNameErr = true;
        this.userNameErrMsg = "請輸入2~5個字";
      } else {
        this.userNameErr = false;
      }
    },
    account: function () {
      const isText = /^[a-zA-Z0-9]+$/;
      if (!isText.test(this.account)) {
        this.accountErr = true;
        this.accountErrMsg = "請勿包含特殊字元";
      } else if (this.account.length < 5) {
        this.accountErr = true;
        this.accountErrMsg = "請勿少於5個字";
      } else {
        this.accountErr = false;
      }
    },
    password: function () {
      const isText = /^[a-zA-Z0-9]+$/;
      const inclde = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,15}$/;
      if (!isText.test(this.password)) {
        this.passwordErr = true;
        this.passwordErrMsg = "請勿包含特殊字元";
      } else if (this.password.length < 8) {
        this.passwordErr = true;
        this.passwordErrMsg = "請勿少於8個字";
      } else if (!inclde.test(this.password)) {
        this.passwordErr = true;
        this.passwordErrMsg = "至少包括一個大小寫字母或數字";
      } else {
        this.passwordErr = false;
      }
    },
    passwordChk: function () {
      if (this.passwordChk !== this.password) {
        this.passwordChkErr = true;
        this.passwordChkErrMsg = "密碼不一致";
      } else {
        this.passwordChkErr = false;
      }
    },
  },
};
</script>

<style lang="scss"></style>
